import React from 'react';
import './App.css';
import {
  Switch,
  Route,
  useLocation,
  Redirect
} from "react-router-dom";
import { useAuth } from '../../Auth/UseAuthHook';
import { ErrorBoundary } from '../ErrorBoundary/ErrorBoundary';
import { TaskList } from '../Tasks/TaskList';
import { useDataStorage } from '../../Store/dataStorage';
import { MessageDialog } from '../Utility/MessageDialog/MessageDialog';
import { PageLayout } from './PageLayout';
import { IconDesigner } from '../IconDesigner/IconDesigner';

function NoMatch() {
  let location = useLocation();

  return (
    <>
      Invalid path: « {location.pathname} »
    </>
  );
}

export const AppComponent = () => {
  const authState = useAuth();
  const dataStorage = useDataStorage();

  if (authState.status === 'Error') {
    return <MessageDialog>Authentication failed: "{authState.errorMessage}"</MessageDialog>
  }

  if (authState.status === 'AuthenticationNeeded') {
    return <MessageDialog>Auth required. AuthenticationNeeded...</MessageDialog>
  }

  if (authState.status === 'TokenReceived') {
    return <MessageDialog>TokenReceived...</MessageDialog>
  }

  return <div className="App">
    <ErrorBoundary>
      <Switch>
        <Route path="/" exact>
          <Redirect to="/tasks" />
        </Route>
        <Route path="/home" exact>
          <PageLayout>
            Home page
            <IconDesigner />
          </PageLayout>
        </Route>
        <Route path="/tasks" exact>
          <TaskList />
        </Route>
        <Route path="*">
          <NoMatch />
        </Route>
      </Switch>
    </ErrorBoundary>

    {dataStorage.status === "fetching" && <MessageDialog>Chargement en cours...</MessageDialog>}
    {dataStorage.status === "saving" && <MessageDialog>Enregistrement en cours...</MessageDialog>}
  </div>;
}